<template>
    <div id="ArticleDetail">
        <div class="article">
            <h3>2019</h3>
            <div class="author">
                <div class="avatar"><img src="../assets/avatar.png" alt=""></div>
                <div class="name"></div>   
                <div class="authorRight"> 
                    <van-button plain type="danger" round size="small" style="margin-right:10px;padding:0 14px" @click="linkhe">TA</van-button>
                </div>
            </div>
            <article>
                <p></p>
                <p>
                
                60</p>
                <p><img src="https://sikeuser.news.cn/shortvideo/fmtimg/86.jpg" alt=""></p>
                <p></p>
                <p></p>
            </article>
            <div class="tousu" @click="tousu"></div>
        </div>
        <div class="card" id="card">
            <img src="../assets/avatar.png" alt="" class="avatarCard">
            <div class="nameCard"></div>
            <div class="btn">
                <van-button plain type="danger" round icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/tel.png" size="small" @click="telto" style="margin-right:10px;padding:0 16px"></van-button>

                <van-button plain type="danger" round icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/weixin.png" size="small" @click="weixin" style="margin-right:10px;padding:0 16px"></van-button>
                
                <van-button plain type="danger" round icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/home.png" size="small" @click="backhome" style="padding:0 16px"></van-button>
            </div>
            <div class="brief">
                <h3></h3>
                <p></p>
            </div>
            <div class="changeBtn" @click="jumptochange"></div>
            <div class="editBtn" @click="jumptoedit"></div>
        </div>
        <van-popup v-model="show">
            <img src="../assets/avatar.png" alt="">
        </van-popup>

        <van-button plain type="danger" round size="small" @click="backbtn" class="backbtn"></van-button>
    </div>
</template>

<script>
export default {
    data () {
      return {
          show:false,
          id: this.$route.query.id
          
      }
  },
  methods: {
      jumptochange(){
          this.$router.push({path: '/EditCard'})
      },
      jumptoedit(){
          this.$router.push({path: '/modify'})
      },
      telto(){
          window.location.href='tel://18911201110'
      },
      weixin(){
          this.show = true
      },
      backhome(){
          this.$router.push({path: '/mycenter'})
      },
      linkhe(){
          console.log(this.$el.querySelector("#card").offsetTop)
          this.$el.querySelector("#card").scrollIntoView();
      },
      tousu(){
          this.$router.push({path: '/tousu'})
      },
      backbtn(){
          this.$router.push({path: '/'})
      }
  },
  mounted () {
      console.log(this.$route.params && this.$route.params.id)
      document.title=""
  }
}
</script>


<style scoped>
    #ArticleDetail{
        position:absolute;
        width:100;
        top:0;
        left:0;
        background:#f2f2f2;
        overflow: hidden;
       
    }
    .article{
        position: relative;
        background:#fff;
        padding:0px 14px;
        font-size:14px;
        text-align: justify;
        line-height: 1.7;
        margin:0;
    }
    .article img{
        width:90%;
        margin:0 auto;
        display: block
    }
    .article h3{     
        text-align: left;
        font-size:18px;
        margin:0;
        padding:20px 0
    }
    .author{
        position: relative;
        width:98%;
        height:66px;
        margin:0 auto;
        border-radius: 10px;
        box-shadow: 0 0 15px  #d9dadc;
    }
    .avatar{
         position:absolute;
         left:10px;
         top:15px;
         width:34px;
    }
    .avatar img{
         width:100%
    }
    .name{
        position:absolute;
        left:50px;
        top:22px;
        font-size:14px
    }
    .authorRight{
        position: absolute;
        right:10px;
        top:20px;
        font-size:10px;
        
    }
    article{
        padding-bottom:50px;
        margin-bottom:80px;
    }
    .tousu{
        position: absolute;
        bottom:10px;
        right:10px;
        color:#586b95
    }
    .card{
        position: relative;
        width:92%;
        margin:0 4%;
        height:260px;
        background:#fff;
        border-radius:10px;
        margin-bottom:50px;
    }
    .avatarCard{
        width:70px;
        margin-top:-35px;
        padding:3px;
        background:#fff;
        border-radius: 100%;
        margin-bottom:10px;
    }
    .nameCard{
        margin-bottom:12px
    }
    .brief h3{
        text-align: left;
        border-left: #ef5054 solid 3px;
        padding-left:10px;
        margin-left:10px;
        font-size:16px
    }
    .brief p{
        text-align: left;
        padding:0 20px;
        font-size:14px;
        line-height: 1.5;
        color:#999
    }
    .changeBtn{
        position: absolute;
        padding:4px 14px;
        background:#e3e3e3;
        color:#666666;
        font-size:14px;
        top:20px;
        right:0;
        border-radius: 20px 0 0 20px
    }
    .editBtn{
        position: absolute;
        padding:10px 20px;
        background:#ef5054;
        color:#fff;
        font-size:15px;
        bottom:-10px;
        right:-5%;
        border-radius: 20px 0 0 20px;
        box-shadow: 0 0 10px  #ef5054;
    }
    .backbtn{
        width:40px;
        height:40px;
        padding:0;
        min-width:40px;
        position:fixed;
        right:10px;
        bottom:100px;
    }
    .van-icon__image{
        width:15px
    }
</style>




