// vue
import Vue from 'vue'
//  App 
import App from './App'

import router from './router'
import axios from 'axios'
import d3 from 'd3'
//  vue router

Vue.config.productionTip = false
Vue.prototype.$http = axios
// Vue.use(d3)
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

// Vue
new Vue({
  // Vue
  el: '#app',
  // <App/>
  template: '<App/>',
  // App<App/>
  components: { App },
  // Vue
  router
}).$mount('#app')



